/* Create two equal columns that floats next to each other */

* {
    box-sizing: border-box;
  }

 

.column {
    float: left;
    width: 50%;
    padding: 10px;
    
    

  }
  
  /* Clear floats after the columns */
  .row:after {
    content: "";
    display: table;
    clear: both;
  }
  .invoiceColor{
    background-color: white;
  }

  .border{
    border: black solid ;
  }